<template>
  <div class="header">
    <mt-header :title="titleMsg" style="user-select: none;">
      <router-link to="/" slot="left">
        <mt-button class="mintui mintui-home-fill"></mt-button>
      </router-link>
      <router-link to="/register" slot="right" v-if="false">
        <mt-button type="primary">{{ login }}&nbsp;|</mt-button>
      </router-link>
      <router-link to="/login" slot="right" v-show="status === '登录'">
        <mt-button type="primary" @click="goLogin">&nbsp;登录</mt-button>
      </router-link>
      <router-link to="/" slot="right" v-show="status === '注销'">
        <mt-button type="primary" @click="goHome">&nbsp;注销</mt-button>
      </router-link>
    </mt-header>
  </div>
</template>

<script>
import { Toast, Indicator  } from 'mint-ui'
export default {
  name: 'HelloWorld',
  data () {
    return {
      titleMsg: '渠道管理系统',
      status: '',
      login: '注册'
    }
  },
  methods: {
    goLogin() {
      this.$router.push('/login')
    },
    goHome() {
      Indicator.open({
        spinnerType: 'fading-circle'
      })
      setTimeout(() => {
        Toast({
          message: '注销成功',
          position: 'bottom'
        })
        sessionStorage.setItem('_userInfo', '')
        sessionStorage.setItem('_mis', '')
        this.getStatus()
        this.$emit('getStatusEnd', 'logout')
        Indicator.close()
        this.$router.push('/')
      }, 1500)
    },
    getStatus() {
      if(sessionStorage.getItem('_userInfo')) {
        if(sessionStorage.getItem('_userInfo') != '') {
          this.status = '注销'
        } else {
          this.status = '登录'
        }
      } else {
        this.status = '登录'
      }
    }
  },
  mounted() {
    this.getStatus()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .mintui-home-fill{
    font-size: 22px;
  }
</style>



